@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@400;600;700&display=swap);
body{margin:0;font-family:"Nunito",sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}.content{display:flex;flex-direction:column;min-height:100vh}.route{display:flex;flex:1 1 auto;justify-content:center;align-items:center;flex-direction:column;background-color:#383a42}
.button{background:#44e384;border:none;border-radius:10px;padding:9px;margin-top:5px;margin-bottom:5px;font-family:"nunito";font-weight:bold;font-size:18px;line-height:25px;letter-spacing:.015em;color:#434343;cursor:pointer;display:flex;justify-content:center;align-items:center}.button:hover{background:#70eaa1}.button.button-small{padding:8px;font-size:14px}.button.button-transparent{background:rgba(0,0,0,0);color:#44e384}
.text-input{border:none;border-radius:10px;outline:none;width:100%;padding:7px;margin-top:5px;margin-bottom:5px;box-sizing:border-box;background:#5b5c5e;color:#dae1e7;font-family:"nunito";font-weight:600;font-size:18px;line-height:25px;letter-spacing:.015em}.input-label{display:inline-block;font-family:"nunito";font-weight:600;font-size:14px;line-height:25px;letter-spacing:.015em;color:#dae1e7;margin-bottom:5px}
.login-popup{position:absolute;box-sizing:border-box;filter:drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));min-width:325px;min-height:185px;padding:15px;background:#292b30;border-radius:10px;display:flex;flex-direction:column;justify-content:space-evenly;align-items:flex-end}.login-error{font-weight:bold;font-size:16px;color:#e34444;margin-top:5px;margin-bottom:5px;width:100%;text-align:center}.login-buttons{display:flex;width:100%;flex-direction:row;justify-content:space-between;align-items:center}.popup-arrow{position:absolute;top:-10px;right:13px;width:28px;height:28px;transform:rotate(45deg);background:#292b30}
.nav-bar{width:100%;height:70px;background:#292b30;display:flex;flex:0 0 auto;flex-direction:row;justify-content:space-between;align-items:center;box-sizing:border-box}.nav-bar .nav-start{padding:14px;padding-left:30px}.nav-bar .nav-end{display:flex;flex-direction:row;align-items:center;justify-content:space-between}.nav-item{display:inline-block;padding:2px;padding-left:19px;padding-right:19px;font-weight:bold;font-size:20px;line-height:68px;text-align:right;letter-spacing:.015em;color:#dae1e7;box-sizing:border-box;border:none;background:none;text-decoration:none;cursor:pointer;position:relative;height:68px}.nav-item:after,.nav-item.active:after{content:"";display:block;border-bottom:3px solid #44e384;bottom:-1px;left:0;width:0;position:absolute;transition:.5s ease}.nav-item:hover:after,.nav-item.active:after{width:100%}.name{font-weight:bold;font-size:26px;line-height:44px;letter-spacing:.02em;color:#dae1e7}
#contact{color:#dae1e7}.contact{text-align:center;color:#dae1e7}.contact-title{font-size:48px;font-weight:bold;line-height:87px;letter-spacing:.02em;margin-bottom:2px;margin-top:0px;color:#dae1e7}.contact-info{width:700px;max-width:80vw;font-size:24px;line-height:33px;letter-spacing:.015em;color:#dae1e7}.link{color:#44e384;text-decoration:none;position:relative}.link:after{content:"";display:block;border-bottom:3px solid #44e384;bottom:-1px;left:0;width:0;position:absolute;transition:.5s ease}.link:hover:after{width:100%}
#dashboard{display:flex;flex-direction:row;justify-content:space-between;align-items:flex-start;height:100%;padding:50px}.dashboard-menu{display:flex;flex-direction:column;justify-content:space-evenly;align-items:flex-start;width:175px;height:200px;border-right:solid 2px #dae1e7}.menu-item{font-family:"Nunito";font-weight:bold;font-size:24px;text-align:right;letter-spacing:.015em;color:#dae1e7;text-decoration:none;border:none;background:none;position:relative;cursor:pointer}.menu-item:after{content:"";display:block;border-bottom:3px solid #44e384;bottom:-1px;left:0;width:0;position:absolute;transition:.5s ease}.menu-item:hover:after,.menu-item.active:after{width:100%}.dashboard-content{display:flex;flex:1 1 auto;flex-direction:column;justify-content:center;align-items:center}.dashboard-files{display:flex;flex-direction:column;align-items:center;justify-content:space-evenly;flex:1 1 auto;width:100%;padding:40px;box-sizing:border-box;color:#dae1e7}.dashboard-account{width:100%;max-width:500px}.token-input{display:flex;justify-content:space-between;align-items:center}.token-copy{height:35px;margin-left:10px;margin-top:30px}.password-update{margin-top:80px}.password-update-error{font-weight:bold;font-size:16px;color:#e34444;margin-top:5px;margin-bottom:5px;width:100%;text-align:center}.password-update-success{font-weight:bold;font-size:16px;color:#44e384;margin-top:5px;margin-bottom:5px;width:100%;text-align:center}.loading{min-height:400px;display:flex;justify-content:center;align-items:center}.file-table{flex:1 1 auto;width:100%;display:grid;grid-template-rows:1fr 14fr;grid-gap:0px 0px;gap:0px 0px;grid-template-areas:"table-header" "content";color:#dae1e7}.table-header{grid-area:table-header;display:grid;grid-template-columns:1.5fr 1fr 1fr 1fr;grid-template-areas:"header-name" "header-size" "header-date" "header-link";padding-bottom:16px;margin-bottom:8px;border-bottom:solid 2px #8c9093;font-weight:bold;font-size:18px;letter-spacing:.015em;color:#dae1e7}.table-header .header-link{text-align:center}.table-row{grid-area:table-content;display:grid;grid-template-columns:1.5fr 1fr 1fr 1fr;grid-template-areas:"row-name" "row-size" "row-date" "row-link";font-weight:600;font-size:18px;letter-spacing:.015em;color:#dae1e7;padding:12px;border-bottom:2px solid #5b5c5e}.table-row:hover{background:#292b30}.table-row .row-name,.table-row .row-date,.table-row .row-size,.table-row .row-link{display:flex;flex-direction:row;align-items:center}.table-row .row-name,.table-row .row-date,.table-row .row-size{pointer-events:none;padding-left:8px;padding-right:8px;text-overflow:ellipsis;white-space:nowrap;overflow:hidden}.table-row .row-link{color:#44e384;cursor:pointer;margin:0px;padding:4px}.table-nav{width:200px;margin-top:45px;display:flex;flex-direction:row;justify-content:space-between;align-items:center;font-weight:bold;font-size:18px;letter-spacing:.015em;color:#dae1e7}.table-nav button{border:none;background:none;width:32px;height:32px;border-radius:5px}.table-nav button:hover:not(.disabled){cursor:pointer;background:#292b30}
.upload-button-wrapper{width:320px;max-width:80vw;height:50px;border-radius:10px;position:relative;overflow:hidden}.upload-progress{content:"";display:block;border-bottom:5px solid #4454e3;bottom:-1px;left:0;width:0;position:absolute;transition:.5s ease;-webkit-transition:.5s ease;pointer-events:none}.upload-label{display:inline-block;width:320px;max-width:80vw;height:50px;line-height:50px;border-radius:10px;background:#44e384;font-weight:bold;font-size:20px;letter-spacing:.015em;text-align:center;color:#434343;-webkit-user-select:none;user-select:none;-moz-user-select:none}.upload-label:hover{cursor:pointer;background:#20d468}
#home{background:url(/static/media/bg.9c2d7d08.jpg);background-size:cover}.info{filter:drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));text-align:center;color:#dae1e7}.credits{position:absolute;bottom:15px;left:15px;font-weight:bold;font-size:20px;color:#dae1e7;text-decoration:none}.title{font-size:64px;font-weight:bold;line-height:87px;letter-spacing:.02em;margin-bottom:2px}.subtitle{margin-top:0px;font-weight:bold;font-size:30px;line-height:41px;letter-spacing:.02em}.description{font-size:24px;line-height:33px;letter-spacing:.015em;filter:drop-shadow(0px 2px 4px rgba(0, 0, 0, 0.35))}.interactions{display:flex;flex-direction:column;align-items:center;width:375px;font-weight:bold;font-size:20px;color:#dae1e7;text-align:center;filter:drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25))}.interaction-buttons{margin-top:20px;width:100%;display:flex;flex-direction:row;justify-content:space-evenly;align-items:center;filter:drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25))}.interaction-buttons .button-github{font-weight:600;font-size:22px;background:rgba(0,0,0,0);color:#fff;border:solid 3px #fff;border-radius:15px;padding:12px}.interaction-buttons .button-github:hover{position:relative;top:-2px}.interaction-buttons .button-sharex{font-weight:600;font-size:22px;background:rgba(0,0,0,0);border:solid 3px #fb4949;color:#fb4949;border-radius:15px;padding:12px}.interaction-buttons .button-sharex:hover{position:relative;top:-2px}.interaction-buttons.wide{width:375px;max-width:90vw}.recent-file{margin-top:14px;display:flex;flex-direction:row;align-items:center}.recent-file span{margin-left:16px}
